  align-items: center;
  justify-content: center;
  margin-bottom: 50px; }

.tariff__wrapper label {
  cursor: pointer;
  display: inline-block;
  height: 8px;
  margin: 25px 17px 0 8px;
  position: relative;
  border-radius: 50%; }

.tariff__wrapper label:nth-of-type(1) {
  left: 14px; }

.tariff__slider-controls label:nth-of-type(2) {
  border: 3px solid #283645;
  width: 6px;
  height: 6px;
  top: 4px;
  left: 4px; }
  .tariff__slider-controls label:nth-of-type(2)::after {
    background-color: transparent; }

.tariff__wrapper label::after {
  content: " ";
  display: block;
  height: 6px;
  left: -6px;
  position: absolute;
  top: 4px;
  width: 6px;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.6); }

.tariff__title-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #283645;
  color: white;
  min-height: 97px; }

.tariff__title {
  margin: 0;
  padding-top: 17px;
  padding-bottom: 2px;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: bold; }

.tariff__cost {
  margin: 0;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: light; }

.tariff__item-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 20px;
  background-color: white; }

.tariff__option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #e5e5e5;
  padding: 14px 39px 14px 42px;
  margin: 0;
  position: relative;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: normal; }

@media (max-width: 320px) {
  .tariff {
    position: relative; }
    .tariff::before {
      position: absolute;
      top: -28px;
      left: -4px;
      content: "";
      width: 0;
      height: 0;
      border-bottom: 26px solid #f2f2f2;
      border-left: 323px solid transparent;
      z-index: -1; }
    .tariff::after {
      position: absolute;
      top: -3px;
      left: 0;
      content: "";
      width: 320px;
      height: 100px;
      z-index: -1;
      background-color: #f2f2f2; }
  .tariff__title-wrapper {
    position: relative; }
    .tariff__title-wrapper::before {
      content: "";
      position: absolute;
      left: 20px;
      width: 2px;
      height: 276px;
      background-color: #e5e5e5; }
    .tariff__title-wrapper::after {
      content: "";
      position: absolute;
      left: 298px;
      width: 2px;
      height: 276px;
      background-color: #e5e5e5; }
  .tariff__title--hit::before {
    content: "";
    position: absolute;
    top: -4px;
    left: 17px;
    width: 80px;
    height: 80px;
    z-index: 1;
    background-image: url("../../img/bg-hit.svg");
    background-repeat: no-repeat;
    background-position: center; }
  .tariff__title--hit::after {
    content: "";
    font-weight: lighter;
    font-size: 14px;
    transform: rotate(-45deg);
    position: absolute;
    top: 7px;
    left: 33px;
    width: 40px;
    height: 40px;
    z-index: 1; } }

@media (min-width: 660px) and (max-width: 660px) {
  .tariff {
    background-color: #f2f2f2;
    position: relative; }
    .tariff::before {
      position: absolute;
      top: -85px;
      left: 0;
      content: "";
      width: 0;
      height: 0;
      border-bottom: 51px solid #f2f2f2;
      border-left: 660px solid transparent;
      z-index: -1; }
    .tariff::after {
      position: absolute;
      top: -35px;
      left: 0;
      content: "";
      width: 660px;
      height: 65px;
      z-index: -1;
      background-color: #f2f2f2; } }

@media (min-width: 660px) {
  .tariff__slider {
    display: none; }
  .tariff {
    background-color: #f2f2f2;
    display: flex;
