@media (min-width: 1200px) and (max-width: 1200px) {
  .tariff {
    padding-top: 66px;
    position: relative; }
    .tariff::before {
      position: absolute;
      top: -130px;
      left: 0;
      content: "";
      max-width: 1200px;
      height: 50px;
      border-bottom: 80px solid #f2f2f2;
      border-left: 1200px solid transparent;
      z-index: 1; } }

