.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Opensans", sans-serif;
  font-style: normal; }

img {
  max-width: 100%;
  height: auto; }
